
import XHeader from 'vux/src/components/x-header/index.vue'
import XTable from 'vux/src/components/x-table/index.vue'

import localStore from 'util/localStore'
export default {
data () {
return {
form: {}




}
},
filters: {
numFilter (value) {
let realVal = ''
if (value) {

realVal = parseFloat(value).toFixed(2)
} else {
realVal = 0.00
}
return realVal
}
},
methods: {
_getDetail () {
this.$tupHttp(this, 'manage-psi/dbTargetEstimate/permission-all/getDbTarToApp', 'POST', {
clientId: localStore.get('TUP_pUid')
}).then(res => {
this.form = res.data.model
})
}
},
created () {
this._getDetail()
},
components: {
XHeader,
XTable
}
}



